import React from "react";
import { Link } from "react-router-dom";
import "./navbar.css";
import { RiVirusLine } from "react-icons/ri";
import { CgHome } from "react-icons/cg";
import { FaSyringe } from "react-icons/fa";
import { AiOutlineSafety } from "react-icons/ai";

const Navbar = () => {
  return (
    <nav className="navbar">
      <RiVirusLine className="covidPNG" />

      <h1 className="mainHead">Covid Tracker</h1>

      <ul className="list">
        <CgHome className="navIcon" />
        <li className="listItem">
          <Link to="/" className="link">
            Home
          </Link>
        </li>

        {/* <FaSyringe className="navIcon" />
        <li className="listItem">
          <Link to="/vaccination" className="link">
            Vaccination
          </Link>
        </li> */}

        <AiOutlineSafety className="navIcon" />
        <li className="listItem">
          <Link to="/precautions" className="link">
            Precautions
          </Link>
        </li>
      </ul>
    </nav>
  );
};

export default Navbar;
