import React from "react";
import "./home.css";
import Popup from "reactjs-popup";
import "reactjs-popup/dist/index.css";
import PublicIcon from "@material-ui/icons/Public";
import { RiFileList3Fill } from "react-icons/ri";

const Home = () => {
  const [worldData, setWorldData] = React.useState({});
  const [data, setData] = React.useState([]);

  const getWorldData = async () => {
    const url = "https://disease.sh/v3/covid-19/all";
    const res = await fetch(url);
    const jsonRes = await res.json();
    setWorldData(jsonRes);
  };

  const getData = async () => {
    const url = "https://disease.sh/v3/covid-19/countries";
    const res = await fetch(url);
    const jsonRes = await res.json();
    setData(jsonRes);
  };

  React.useEffect(() => {
    getWorldData();
    getData();
  }, []);

  return (
    <>
      <div className="worldData">
        <PublicIcon className="worldIcon" />
        <h3 className="data">Worldwide :</h3>

        <h4 className="wRecovered data">Recovered : {worldData.recovered}</h4>
        <h4 className="wDeaths data">Deaths : {worldData.deaths}</h4>
        <h4 className="wActive data">Active : {worldData.active}</h4>

        <Popup
          trigger={<button className="more_button">More</button>}
          position="left center"
        >
          <div className="modal">
            <h3 className="modal_country">World Data</h3>
            <h4 className="modal_recovered">
              Today's Recovered : {worldData.todayRecovered}
            </h4>

            <h4 className="modal_deaths">
              Today's Deaths : {worldData.todayDeaths}
            </h4>

            <h4 className="modal_cases">
              Today's Cases : {worldData.todayCases}
            </h4>

            <h4 className="modal_critical">Critical : {worldData.critical}</h4>
          </div>
        </Popup>
      </div>

      <div className="c_data">
        <RiFileList3Fill className="c_headPNG" />
        <h2 className="c_head">Country wise Info</h2>
      </div>

      {data.map((country) => {
        return (
          <div key={country.countryInfo._id} className="countryInfo">
            <div className="fan">
              <h3>
                {country.country} | {country.countryInfo.iso2}
              </h3>
              <img src={country.countryInfo.flag} alt={country.country}></img>
            </div>

            <div className="info">
              <h4 class="c_recovered">Recovered : {country.recovered}</h4>
              <h4 class="c_deaths">Deaths : {country.deaths}</h4>
              <h4 class="c_active">Active : {country.active}</h4>

              <Popup
                trigger={<button className="more_button"> More</button>}
                position="left center"
              >
                <div className="modal">
                  <h3 className="modal_country">
                    {country.country} | {country.countryInfo.iso2}
                  </h3>

                  <h4 className="modal_recovered">
                    Today's Recovered : {country.todayRecovered}
                  </h4>

                  <h4 className="modal_deaths">
                    Today's Deaths : {country.todayDeaths}
                  </h4>

                  <h4 className="modal_cases">
                    Today's Cases : {country.todayCases}
                  </h4>

                  <h4 className="modal_critical">
                    Critical : {country.critical}
                  </h4>
                </div>
              </Popup>
            </div>
          </div>
        );
      })}
    </>
  );
};

export default Home;
