import React from "react";
import "./precautions.css";
import { AiOutlineSafety } from "react-icons/ai";
import { FaSyringe } from "react-icons/fa";
import { RiSurgicalMaskFill } from "react-icons/ri";
import { IoIosPeople } from "react-icons/io";
import { RiHandSanitizerFill } from "react-icons/ri";
import { RiHomeHeartFill } from "react-icons/ri";
import { GoAlert } from "react-icons/go";

const Precautions = () => {
  return (
    <>
      <div className="head">
        <AiOutlineSafety className="head_icon" />
        <h1>Precautions to take</h1>
      </div>

      <div className="precautions">
        <p className="odd">
          <FaSyringe className="icon" />
          Get vaccinated as fast as possible.
        </p>

        <p>
          <GoAlert className="icon" />
          Avoid close distancing.
        </p>

        <p className="odd">
          <RiHandSanitizerFill className="icon" />
          Sanitize hands frequently.
        </p>

        <p>
          <IoIosPeople className="icon" />
          Avoid gatherings.
        </p>

        <p className="odd">
          <RiSurgicalMaskFill className="icon" />
          Wear Mask.
        </p>

        <p>
          <RiHomeHeartFill className="icon" />
          Stay home. Stay Safe
        </p>
      </div>
    </>
  );
};

export default Precautions;
